import React from "react";
import { GlobalStyle } from "./Styles";
import { HashRouter as Router, Routes, Route } from "react-router-dom";
import Laboratorio from "./pages/Laboratorio";
import { GlobalProvider } from "./context/GlobalContext"; // Importa el proveedor del contexto
import './App.css';

function App() {
  return (
    <GlobalProvider> {/* Envolver la aplicación en el proveedor */}
      <div className="App">
        <Router>
          <Routes>
            <Route path="/" element={<Laboratorio />} />
          </Routes>
        </Router>
        <GlobalStyle />
      </div>
    </GlobalProvider>
  );
}

export default App;
